___CSS_LOADER_EXPORT___.push([module.id, `.event-log-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    background-color: #2e2e2e;
    color: #ffffff;
    border-radius: 10px;
  }
  
  .search-sort-bar {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .search-sort-bar input {
    flex: 1;
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #555;
    border-radius: 5px;
    background-color: #444;
    color: #fff;
  }
  
  .search-sort-bar button {
    padding: 10px 20px;
    margin-right: 10px;
    background-color: #007bff; /* 파란색 버튼 */
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .search-sort-bar button:hover {
    background-color: #0056b3; /* 호버시 색상 변경 */
  }
  
  .sort-options button {
    padding: 10px 20px;
    margin-left: 5px;
    background-color: #555;
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .sort-options button.active {
    font-weight: bold;
    background-color: #777;
  }
  
  .event-log-list {
    list-style: none;
    padding: 0;
  }
  
  .event-log-item {
    background-color: #333;
    margin-bottom: 10px;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #555;
  }
  
  .event-log-item strong {
    color: #ffcc00; /* 강조할 부분을 노란색으로 */
  }
  
  .event-log-item p {
    margin: 5px 0;
